    padding: 0 20px 0 20px;
    background-color: #fff;

    .wp-heading-inline {
        margin-bottom: 20px;
    }

    .myGallery {
        position: relative;
        display: grid;
        grid-gap: 15% 10%;
        grid-template-columns: repeat(3, 1fr);
        grid-auto-rows: 1fr;

        .item {
            position: relative;
            img {
                width: 100%;
                cursor: pointer;
            }

            .bottom-center {
                position: absolute;
                width: 17px;
                height: 17px;
                background-color: white;
                border-radius: 50%;
                color: black;
                bottom: 0;
                left: 6px;
                transform: translate(5px, 10px);
                box-shadow: 0px 3.21062px 6.42124px rgb(0 0 0 / 25%);
                text-align: center;
                font-size: 10px;
            }
        }
    }

    .badge_loader {
        position: relative;
    }
}
</style>
